<template>
  <div>
    <el-button @click="delVolume" size="small" icon="el-icon-delete" type="danger">{{  $t('VolumeBtn_delete')}}</el-button>
    <el-button @click="addVolume" size="small" icon="el-icon-circle-plus-outline" type="success">{{  $t('VolumeBtn_Addvolume') }}</el-button>
  </div>
</template>

<script>
import http from '@/utils/http'
export default {
  props: ['checkVolume'],
  methods: {
    // 添加
    addVolume () {
      this.$router.push('/DataVolume/AddVolume')
    },
    // 删除数据卷
    delVolume () {
      if (this.checkVolume.length < 1) {
        return this.$message({
          showClose: true,
          message: '未选择数据卷！',
          type: 'warning'
        })
      }
      for (let a = 0; a < this.checkVolume.length; a++) {
        const docker = `curl --unix-socket /var/run/docker.sock -s -X DELETE "http:/v1.40/volumes/${this.checkVolume[a].Name}"` // 删除数据卷
        http({
          url: '/docker',
          method: 'POST',
          timeout: 600000000,
          data: {
            docker
          }
        }).then(res => {
          if (res.Code !== 200) {
            return this.$message({
              showClose: true,
              message: '删除失败！',
              type: 'error'
            })
          }
          if (res.message) {
            return this.$message({
              showClose: true,
              message: res.message,
              type: 'error'
            })
          }
          this.$message({
            showClose: true,
            message: '数据卷删除成功！',
            type: 'success'
          })
          this.$emit('successDel')
        })
      }
    }
  }
}
</script>
