<template>
  <div>
    <VolumeBtn :checkVolume="checkVolume" @successDel="fetchVolume"></VolumeBtn>
    <SerchInput :serchdata="volumeList" @serchActive="serchActive" style="margin-top: 20px"></SerchInput>
    <VolumeTable v-if="volumeList" :dataList="AfterChangeData" @changecheck="changecheck"></VolumeTable>
    <div class="page" v-if="volumeList">
      <div class="pagenum">
        <el-select v-model="pageNum" :placeholder="$t('DataVolume_plsSelectActiveRange')" style="width: 55px" size="mini">
          <el-option :label="item" :value="item" v-for="item in Math.ceil((serchList ? serchList.length : volumeList.length)/pageSize)" :key="item"></el-option>
        </el-select>
        {{ $t('Page_Page') }}
      </div>
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum"
        :page-size="pageSize"
        layout="prev, pager, next"
        :total="serchList ? serchList.length : volumeList.length"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Form, FormItem, Select, Option, Input, Button, Popover, Table, TableColumn, Pagination } from 'element-ui'
import post from '@/utils/post'
import SerchInput from '@/components/volume/SerchInput'
import VolumeTable from '@/components/volume/VolumeTable'
import VolumeBtn from '@/components/volume/VolumeBtn'
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Select)
Vue.use(Input)
Vue.use(Option)
Vue.use(Button)
Vue.use(Popover)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Pagination)

export default {
  data () {
    return {
      volumeList: null, // 请求回来的volume
      serchList: null, // 搜索的volume
      checkVolume: [], // 选中的volume
      pageNum: 1, // 当前页
      pageSize: 7 // 每页显示数量
    }
  },
  mounted () {
    this.fetchVolume()
  },
  computed: {
    // 返回当前页面显示的数据
    AfterChangeData () {
      let start = (this.pageNum - 1) * this.pageSize
      let end = this.pageNum * this.pageSize
      return (this.serchList ? this.serchList.slice(start, end) : this.volumeList.slice(start, end))
    }
  },
  methods: {
    // 请求volume数据列表
    fetchVolume () {
      const docker = 'curl --unix-socket /var/run/docker.sock -s "http:/v1.40/volumes"' // 查询卷列表
      post({ docker }, (res) => {
        this.volumeList = res.Volumes
      }, this)
    },
    // 处理选中
    changecheck (data) {
      this.checkVolume = data
    },
    // 分页大小改变
    handleSizeChange (val) {
      this.pageSize = val
    },
    // 当前页数改变
    handleCurrentChange (val) {
      this.pageNum = val
    },
    // 处理搜索返回的匹配数据列表
    serchActive (data) {
      this.serchList = data
    }
  },
  components: {
    SerchInput,
    VolumeTable,
    VolumeBtn
  }
}
</script>

<style lang="scss" scoped>
.page{
  margin-top: 30px;
  float: right;
  display: flex;
  line-height: 28px;
  margin-right: 80px;
  .pagenum{
    margin-right: 10px;
  }
}
</style>
