<template>
  <div class="serch">
    <el-input v-model="state" :placeholder="$t('SerchInput_Pleaseinputthecontent')" @input="filterdata"></el-input>
  </div>
</template>

<script>
export default {
  data () {
    return {
      state: null, // 输入框内容
      newdata: null // 筛选后的数据
    }
  },
  props: ['serchdata'],
  methods: {
    // 筛选数组，以及input事件
    filterdata () {
      this.newdata = this.state ? this.serchdata.filter((item) => {
        return item.Name.toLowerCase().indexOf(this.state.toLowerCase()) !== -1
      }) : this.serchdata
      this.$emit('serchActive', this.newdata)
    },
    // 鼠标按下选择
    itemclick (item) {
      this.state = item.Name
      this.filterdata()
    }
  }
}
</script>

